/* usage : 

    @media VARIABLE_NAME {
        // style in here 
    }
*/
html {
  box-sizing: border-box;
  font-size: 100%;
  margin: 0;
}

*, *:before, *:after {
  box-sizing: inherit;
  margin: 0;
}

body {
  overflow: hidden;
  color: #63687e;
  font-family: "Inter", sans-serif;
  line-height: 1.3;
  display: flex;
  flex-flow: column;
  align-items: center;
  font-weight: 700;
  transition: background-color 0.5s;
}
body > .top-pattern {
  border-radius: 0 0 25px 25px;
  height: 15.25rem;
  position: absolute;
  width: 100%;
}
body.light-theme {
  background-color: #ffffff;
}
body.light-theme > .top-pattern {
  background-color: #f5f7ff;
}
body.dark-theme {
  background-color: #1e212a;
}
body.dark-theme a {
  color: #ffffff;
}
body.dark-theme > .top-pattern {
  background-color: #212433;
}
body .init {
  width: 100%;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #f7f7f7;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  align-items: center;
  animation-duration: 10s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
  animation-name: loadingScreen;
  animation-timing-function: linear;
  background: #f6f7f8;
  background: linear-gradient(90deg, #fff 10%, #e9f4ff 55%, #f5f5f5 71%, #fff 94%);
}
body .init .init-cont {
  max-width: 1400px;
  width: 95%;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  align-items: center;
}
body .init .init-cont img {
  width: 30%;
}
@media only screen and (max-width: 600px) {
  body .init .init-cont img {
    width: 50%;
  }
}
body .init .init-cont .init-text {
  text-align: center;
}

main {
  display: flex;
  max-width: 1100px;
  width: 95%;
  padding: 2px;
  margin: 0 auto;
  position: relative;
  margin: 2.5rem;
  flex-flow: column;
  border-radius: 15px;
}
main .dashtop {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: baseline;
  margin-bottom: 3rem;
}
@media only screen and (max-width: 600px) {
  main .dashtop .dashinfo {
    border-bottom: 2px solid gray;
    padding-bottom: 1rem;
    margin-bottom: 1rem;
    flex: 0 1 97%;
  }
  main .dashtop .dashinfo h1 {
    font-size: 1.25rem;
  }
}
main .dashtop .togglediv {
  display: flex;
  align-items: baseline;
}
@media only screen and (max-width: 600px) {
  main .dashtop .togglediv {
    justify-content: space-between;
    width: 95%;
  }
}
main .dashtop .togglediv > p {
  margin-right: 1rem;
  transition-duration: 1s;
}
main .followers-stats {
  display: flex;
  justify-content: space-around;
  flex-flow: row wrap;
}
main .overview {
  margin: 2rem 0rem;
  margin-bottom: 1rem;
}
main .overview h2 {
  font-size: 1.5rem;
}
main .overview-stats {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
}
main.light-theme h1 {
  color: #000000;
  transition-duration: 1s;
}
main.dark-theme h1 {
  color: #ffffff;
  transition-duration: 1s;
}
main.dark-theme h2 {
  color: #ffffff;
}
main.dark-theme .dashtop p {
  color: #8b97c6;
}

@keyframes loadingScreen {
  0% {
    background-position: -468px 0;
  }
  100% {
    background-position: 468px 0;
  }
}
/* The switch - the box around the slider */
.switch {
  position: relative;
  display: inline-block;
  width: 3rem;
  height: 1.5rem;
  transform: rotate(180deg);
  /* Hide default HTML checkbox */
}
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #aeb3cb;
  transition: 0.4s;
  overflow: hidden;
  /* Rounded sliders */
}
.slider.round {
  border-radius: 34px;
}
.slider.round::before {
  border-radius: 50%;
}
.slider::before {
  position: absolute;
  content: "";
  height: 1.125rem;
  width: 1.125rem;
  left: 4px;
  bottom: 3px;
  background-color: #ffffff;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  z-index: 3;
}
.slider::after {
  position: absolute;
  z-index: 2;
  background: linear-gradient(90deg, #3eda82 0%, #378fe6 100%);
  opacity: 0;
  content: "";
  height: 100%;
  width: 100%;
  transition: opacity 0.7s;
  border-radius: 34px;
}

input:checked + .slider::after {
  opacity: 1;
}

input:hover + .slider::after {
  opacity: 1;
}

input:checked + .slider:before {
  transform: translateX(22px);
  background-color: #212433;
}

.follwers-card {
  width: 15.9375rem;
  height: 13.5rem;
  border-radius: 4px;
  padding: 1.5rem 4.1875rem;
  text-align: center;
  transition: background-color 0.5s;
  margin: 0.5rem;
  position: relative;
  display: flex;
  flex-flow: column;
  align-items: center;
}
@media only screen and (max-width: 600px) {
  .follwers-card {
    max-width: 100%;
    width: 95%;
  }
}
.follwers-card::before {
  content: "";
  height: 4px;
  position: absolute;
  width: 100%;
  left: 0px;
  top: 0px;
  animation-duration: 10s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
  animation-name: placeHolderShimmer;
  animation-timing-function: linear;
  background: #f6f7f8;
  background: linear-gradient(90deg, #ff3df2 15%, #3356f0 34%, #47f727 63%, #ff3df2 94%);
  border-radius: 4px 4px 0px 0px;
  transition-duration: 1s;
}
.follwers-card:hover {
  cursor: pointer;
}
.follwers-card > .account {
  display: flex;
  margin-bottom: 1rem;
  align-items: center;
}
.follwers-card > .account > img {
  width: 20px;
}
.follwers-card > .account > p {
  font-size: 1rem;
  font-weight: 700;
  margin-left: 0.5rem;
}
.follwers-card .follwers-number .foll-num {
  font-size: 3rem;
  font-weight: 700;
  color: #000000;
}
.follwers-card .follwers-number > p:nth-child(2) {
  font-size: 0.8rem;
  font-weight: 400;
  letter-spacing: 5px;
  text-transform: uppercase;
}
.follwers-card .follwers-percentage {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  margin-top: 2rem;
  align-items: center;
}
.follwers-card .follwers-percentage > img {
  height: 5px;
  margin-right: 0.2rem;
}
.follwers-card .follwers-percentage > p {
  font-size: 0.8rem;
}
.follwers-card .follwers-percentage.up {
  color: #1db489;
  transition-duration: 0.5s;
}
.follwers-card .follwers-percentage.up > img {
  content: url("../../images/icon-up.svg");
}
.follwers-card .follwers-percentage.down {
  color: #dc414c;
  transition-duration: 0.5s;
}
.follwers-card .follwers-percentage.down > img {
  content: url("../../images/icon-down.svg");
}
.follwers-card.light-theme {
  background-color: #f0f2fa;
}
.follwers-card.light-theme:hover {
  background-color: #e0e2f0;
}
.follwers-card.dark-theme {
  background-color: #252a41;
}
.follwers-card.dark-theme .account p {
  color: #8b97c6;
}
.follwers-card.dark-theme .follwers-number {
  color: #8b97c6;
}
.follwers-card.dark-theme .follwers-number .foll-num {
  color: #ffffff;
}
.follwers-card.dark-theme:hover {
  background-color: #333a57;
}
.follwers-card.youtube .account > img {
  content: url(../../images/icon-youtube.svg);
}
.follwers-card.youtube::before {
  background: #c4032a;
  animation-name: none;
}
.follwers-card.facebook .account > img {
  content: url(../../images/icon-facebook.svg);
}
.follwers-card.facebook::before {
  background: #198ff5;
  animation-name: none;
}
.follwers-card.twitter .account > img {
  content: url(../../images/icon-twitter.svg);
}
.follwers-card.twitter::before {
  background: #1ca0f2;
  animation-name: none;
}
.follwers-card.instagram .account > img {
  content: url(../../images/icon-instagram.svg);
}
.follwers-card.instagram::before {
  background: linear-gradient(70deg, #fdc468 0%, #df4996 100%);
  animation-name: none;
}

@keyframes placeHolderShimmer {
  0% {
    background-position: -468px 0;
  }
  100% {
    background-position: 468px 0;
  }
}
